<template>
  <div class="cover-container" @click="showCover">
    <div class="cover-wrap">
        <img
          :src="coverImage ? coverImage : src "
          ref="cover-img"
          class="cover"
        >
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      width="70%"
    >
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name='first'>
          <imageList
            :isShowAddImage="false"
            :isShowAction="false"
            :isselected="true"
            ref='imgList'
          ></imageList>
        </el-tab-pane>
        <el-tab-pane label="" name='second'>
          <input type="file" @change="onChange" ref="file">
          <div>
            <img src="" style="width:100px;" ref="previewImage">
          </div>
        </el-tab-pane>
      </el-tabs>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="onCoverConfirm"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { uploadImage } from '@/api/image'
import imageList from '@/views/image/components/image-list.vue'
export default {
  name: 'coverIndex',
  components: {
    imageList
  },
  props: {
    coverImage: {
      type: String
    }
  },
  data () {
    return {
      dialogVisible: false,
      activeName: 'first',
      src: ''
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    showCover () {
      this.dialogVisible = true
    },
    // 
    onChange () {
      const file = this.$refs.file.files[0]
      const blob = window.URL.createObjectURL(file)
      this.$refs.previewImage.src = blob
    },
    // 
    onCoverConfirm () {
      //  tab  
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        // 
        const fd = new FormData()
        fd.append('image', file)
        uploadImage(fd).then(res => {
          console.log(res)
          // 
          this.dialogVisible = false
          // 
          this.$emit('uploadCover', res.data.data.url)
          // 
          this.src = res.data.data.url
        })
      } else {
        //  tab  
        if (this.activeName === 'first') {
          // ref 
          const imageList = this.$refs.imgList
          const { selected } = imageList
          if (selected === null) {
            this.$message('')
            return
          }
          // 
          this.dialogVisible = false
          // 
          this.$emit('uploadCover', imageList.images[selected].url)
          // 
          this.src = imageList.images[selected].url
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cover-container{
  .cover-wrap{
    width: 100px;
    height: 100px;
    border: 1px double #ccc;
    display: flex;
  }
  .cover{
    max-width: 100px;
    height: 100px;
  }
}
</style>
