<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item><a href="/">{{id ? '' : ''}}</a></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form ref="form"
        :model="article"
        :rules="formRules"
         label-width="60px"
      >
        <el-form-item label="" prop="title" class="title">
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-tiptap
            v-model="article.content"
            :extensions="extensions"
            height="400"
            placeholder=""
            lang="zh"
          ></el-tiptap>
        </el-form-item>
        <el-form-item label="" class="cover">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!--  -->
          <template v-if="article.cover.type > 0">
            <div class="cover-item">
              <uploadCover
                class="img"
                v-for="(cover, index) in article.cover.type"
                :key="index"
                :coverImage="article.cover.images[index]"
                @uploadCover="onuploadCover(index, $event)"
              />
              <!-- <uploadCover
                class="img"
                v-for="(cover, index) in article.cover.type"
                :key="index"
                v-model="article.cover.images[index]"
              /> -->
            </div>
          </template>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="">
              <el-option
                v-for="(item, index) in channels"
                :key="index"
                :label="item.name"
                :value="item.id"
                ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="id">
          <el-button type="primary" @click="onPublish(false)"></el-button>
        </el-form-item>
        <el-form-item v-else>
          <el-button type="primary" @click="onPublish(false)"></el-button>
          <el-button @click="onPublish(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { getArticleChannels, addArticle, getupdateArticle, updateArticle } from '@/api/user'
import uploadCover from './components/upload-cover.vue'
import { uploadImage } from '@/api/image'
import {
  //  extensions
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Image,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  TextAlign,
  Indent,
  LineHeight,
  HorizontalRule,
  Preview,
  Fullscreen
} from 'element-tiptap'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
export default {
  name: 'Publish',
  components: {
    'el-tiptap': ElementTiptap,
    uploadCover
  },
  props: {},
  data () {
    return {
      channels: [], // 
      article: {
        title: '', // 
        content: '', // 
        cover: {
          type: 0, //  -1:0-1-1 3-3
          images: [] // 
        },
        channel_id: null // id
      },
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          // { required: true, message: '', trigger: 'blur' },
          // { trigger: 'change' }
          // 
          {
            validator (rule, value, callback) {
              if (value === '<p></p>' || value.length === 0) {
                // 
                callback(new Error(''))
              } else {
                // 
                callback()
              }
            },
            trigger: 'change'
          }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      },
      id: '',
      isShow: true,
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }), // 
        new Bold({ bubble: true }), // 
        new Image({
          //  base64 
          // 
          // uploadRequest() ,
          uploadRequest (file) {
            const fd = new FormData()
            fd.append('image', file)
            // promis
            return uploadImage(fd).then(res => {
              console.log(res)
              // 
              return res.data.data.url
            })
          }
        }),
        new Underline({ bubble: true }), // 
        new Italic(), // 
        new Strike(), // 
        new ListItem(),
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(),
        new TextAlign(),
        new Indent(),
        new LineHeight(),
        new HorizontalRule(),
        new Preview(),
        new Fullscreen()
      ]
    }
  },
  computed: {},
  watch: {},
  created () {
    // 
    this.getArticleChannels()
    // 
    const id = this.$route.query.id
    this.id = id
    if (this.id) {
      this.loadArticle(this.$route.query.id)
    }
  },
  mounted () {},
  methods: {
    getArticleChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    //  / 
    onPublish (draft = false) {
      // 
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        } else {
          // 
          if (this.$route.query.id) {
            updateArticle(this.$route.query.id, this.article).then(() => {
              this.$message({
                message: '',
                type: 'success'
              })
              this.$router.back()
            })
          } else {
            // 
            addArticle(this.article, draft).then(res => {
              console.log(res)
              this.$message({
                message: `${draft ? '' : ''}`,
                type: 'success'
              })
              this.$router.back()
            })
          }
        }
      })
    },
    // 
    loadArticle (id) {
      getupdateArticle(id).then(res => {
        this.article = res.data.data
      })
    },
    // 
    onuploadCover (index, url) {
      this.article.cover.images[index] = url
    }
  }
}
</script>

<style lang="less" scoped>
.title{
  width: 500px;
}
.cover{
  display:flex;
  flex-direction: column;
  .cover-item{
    display: flex;
    .cover-container{
      margin-right: 10px;
    }
  }
}
</style>
